            <div >
            <router-link to="/MKHomePage"> <img src="../../img/home.png" class="home"></router-link>   
            </div>
            <div class="dropdown">

                 <img  src="../../img/menu.png" class="icon">   
                
                 <div class="dropdown-content">
                    <a href="#"><router-link to="/MKEnviroment"> </router-link></a>
                    <a href="#"><router-link to="/MKRegional">  </router-link></a>
                    <a href="#"><router-link to="/MKTodayPage"> </router-link></a>
                    <a href="#"><router-link to="/MKQuizzes"></router-link></a>
                    <a href="#"><router-link to="/MKFunPage"></router-link></a>
                    <a href="#"><router-link to="/MKAbout"> </router-link></a>
                    <span  class="homeD">
                        <a hre="#"><router-link to="/" ></router-link> </a>
                    </span>
                </div>
             </div>
            
        <li class="logo"> <img src="../../img/logo4.png"></li>  
        </ul>
     </div>

    <div class="quizzes">
      <h1>{{ quiz.title }}</h1>
    
      <h2></h2>
      <ol>

        <li v-for="(question, index) in quiz.questions" :key="index">
          <p>{{ question.question }}</p>
          <ul class="list">
            <li v-for="(answer, answerIndex) in question.answers" :key="answerIndex" class="symbol">
              <button @click="selectAnswer(index, answer)" :class="{ selected: answers[index] === answer }" 
              :disabled="submit && answers[index] !== answer" >{{ answer }}</button>
            </li>
          </ul>
        </li>

      </ol>
   
    <div>
    <div >
      <button @click="submitQuiz" :disabled="!allQuestionsAnswered || submit">Submit Quiz</button>
    </div>

    <div v-if="allQuestionsAnswered && submit" class="score">
      <span v-if="score<=6">
        <h2>        !</h2>
        <img src="../../img/mavrovo.jpg" alt=""  class="park">
      </span>
      <span v-if="score>6 & score<=11">
        <h2>        !</h2>
        <img src="../../img/pelister.jpg" alt="" class="park">
      </span>
      <span v-if="score>11">
        <h2>        !</h2>
        <img src="../../img/galicica.jpg" alt=""  class="park">
      </span>
      <button @click="restartQuiz"> ?</button>
    </div>
     </div>
  </div>
  <div class="footer">
        <h1> !</h1>
        
        <table>
            <tr>
                <td>
                  <img src="../../img/insta.png">  
                </td>
                <td><p>@ecosrekja</p></td>
            </tr>
            <tr>
                <td>
                    <img src="../../img/face.png">
               
                </td>
                <td><p>@ecosrekja</p></td>
            </tr>
            <tr>
                <td>    
               <img src="../../img/mail.png"> 
                </td>
                <td><p><a href="mailto:ecosrekja@gmail.com">ecosrekja@gmail.com</a></p></td>
            </tr>
        </table>
             
        
    </div>
  
</template>


<script>
export default {
  name:'MKQuizOneF',
  data() {
    return {
      quiz: {
        title: "      ?",
        questions: [
          {
            question: "     ?",
            answers: [")    ", ")    ",
            ")    "],
          },
          {
            question: "      ?",
            answers:[")   ",
             ")      ", ")     "],
          },
          {
            question: "    ?",
            answers: [")     ", 
            ")     ", 
            ")    "],
          },
          {
            question: "    ?",
            answers: [") ,      ",
             ")       ", 
            ") ,     "],
          },
          {
            question: "      ?",
            answers: [") ,     ,   ",
            ") ,         ",
            ")  ,       "],
          }
        ],
      },
      answers: [],
      score: 0,
      submit: false
    };
  },
  computed: {
    allQuestionsAnswered() {
      return this.answers.length === this.quiz.questions.length;
    }
  },
  methods: {
    selectAnswer(questionIndex, answer) {
        this.answers[questionIndex] = answer;
    },
    restartQuiz() {
      this.answers = [];
      this.score = 0;
      this.submit = false;
    },
    submitQuiz() {
  this.submit = true;
  this.score = 0; 

  this.answers.forEach((answer, questionIndex) => {
    const selectedIndex = this.quiz.questions[questionIndex].answers.indexOf(answer);
    if (selectedIndex !== -1) {
      this.score += selectedIndex + 1; 
    }
  });
}
  },
};
</script>
